import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: '/', name: 'main', component: () => import('@/views/main/index') },
    {
      path: '/overview',
      name: 'overview',
      redirect: '/overview/index',
      component: () => import('@/views/overview/index'),
      children: [
        { path: 'index', component: () => import('@/views/overview/overview'), name: 'index' },
        { path: 'strategy', component: () => import('@/views/overview/strategy'), name: 'strategy' },
        { path: 'content', component: () => import('@/views/overview/content'), name: 'content' },
        { path: 'security', component: () => import('@/views/overview/security'), name: 'security' }
      ]
    },
    { path: '/alertview', name: 'alertview', component: () => import('@/views/alertview/index') }
  ]
})



